.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  transform-origin: center;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 0 16px;
  transition: all .3s;
  display: flex;
  position: relative;
}

.card__imac-image {
  width: 160px;
  height: 134px;
  margin: 32px 0;
}

.card__title {
  color: #060b35;
  margin: 8px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  text-decoration: none;
  display: flex;
}

.card__code {
  color: #616070;
  width: 100%;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__ratings {
  width: 100%;
  margin-top: 8px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0 0 0 auto;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 10px 0 2px;
  display: flex;
}

.card__price-word {
  color: #616070;
  font-size: 12px;
}

.card__price-number {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover {
